import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import styles from "../css/todo.module.css";
import {
  editTodoFailure,
  editTodoRequest,
  editTodoSuccess,
  showTodoFailure,
  showTodoRequest,
  showTodoSuccess
} from "../store/actions";

export const ViewTodo = () => {
  const params = useParams();

  const dispatch = useDispatch();

  const state = useSelector((state) => ({
    isLoading: state.loading,
    isError: state.error,
    todo: state.oneTodo
  }));

  const { isLoading, isError, todo } = state;

  const [task, setTask] = useState("");
  const [description, setDescription] = useState("");

  const handleUpdate = () => {
    dispatch(editTodoRequest());
    axios
      .put(`http://localhost:5555/todo/${params.id}`, {
        ...todo,
        task,
        description
      })
      .then(() => {
        dispatch(editTodoSuccess());
        getTodo();
      })
      .catch((error) => {
        dispatch(editTodoFailure(error.message));
      });
  };

  const getTodo = () => {
    dispatch(showTodoRequest());
    axios
      .get(`http://localhost:5555/todo/${params.id}`)
      .then(({ data }) => {
        dispatch(showTodoSuccess(data));
        setTask(data.task);
        setDescription(data.description);
      })
      .catch((error) => {
        dispatch(showTodoFailure(error.message));
      });
  };

  useEffect(() => getTodo(), [dispatch]);

  return isLoading ? (
    <div>
      <h1>Loading...</h1>
    </div>
  ) : isError ? (
    <div>
      <h1>{isError}</h1>
    </div>
  ) : (
    <div className={styles.todoInput}>
      <h1>Title</h1>
      <input
        type="text"
        style={{
          minWidth: "400px",
          maxWidth: "400px",
          width: "400px",
          height: "30px",
          minHeight: "30px",
          maxHeight: "30px"
        }}
        value={task}
        onChange={(e) => setTask(e.target.value)}
      />
      <h1>Description</h1>
      <textarea
        style={{
          minWidth: "400px",
          maxWidth: "400px",
          width: "400px",
          height: "150px",
          minHeight: "150px",
          maxHeight: "150px"
        }}
        value={description}
        onChange={(e) => setDescription(e.target.value)}
      />
      <button onClick={handleUpdate}>Update</button>
    </div>
  );
};
