import { useDispatch, useSelector } from "react-redux";
import { TodoInput } from "./todoInput";
import { v4 as uuid } from "uuid";
import styles from "../css/todo.module.css";
import { NavLink } from "react-router-dom";
import {
  addTodoFailure,
  addTodoRequest,
  addTodoSuccess,
  getTodoFailure,
  getTodoRequest,
  getTodoSuccess,
  editTodoRequest,
  editTodoSuccess,
  editTodoFailure,
  deleteTodoRequest,
  deleteTodoSuccess,
  deleteTodoFailure
} from "../store/actions";
import axios from "axios";
import { useEffect } from "react";

export const Todos = () => {
  const dispatch = useDispatch();

  const state = useSelector((state) => ({
    todos: state.todo,
    isLoading: state.loading,
    isError: state.error
  }));

  const { todos, isLoading, isError } = state;

  const handleToggle = (obj) => {
    dispatch(editTodoRequest());
    axios
      .put(`http://localhost:5555/todo/${obj.id}`, {
        ...obj,
        status: !obj.status
      })
      .then(() => {
        dispatch(editTodoSuccess());
        getTodo();
      })
      .catch((error) => {
        dispatch(editTodoFailure(error.message));
      });
  };

  const handleDelete = (obj) => {
    dispatch(deleteTodoRequest());
    axios
      .delete(`http://localhost:5555/todo/${obj.id}`)
      .then(() => {
        dispatch(deleteTodoSuccess());
        getTodo();
      })
      .catch((error) => {
        dispatch(deleteTodoFailure(error.message));
      });
  };

  const handlePayload = (payload) => {
    dispatch(addTodoRequest());
    axios
      .post("http://localhost:5555/todo", payload)
      .then(() => {
        dispatch(addTodoSuccess());
        getTodo();
      })
      .catch((error) => {
        dispatch(addTodoFailure(error.message));
      });
  };

  const getTodo = () => {
    dispatch(getTodoRequest());
    axios
      .get("http://localhost:5555/todo")
      .then(({ data }) => {
        dispatch(getTodoSuccess(data));
      })
      .catch((error) => {
        dispatch(getTodoFailure(error.message));
      });
  };

  useEffect(() => getTodo(), [dispatch]);

  return isLoading ? (
    <div>
      <h1>Loading...</h1>
    </div>
  ) : isError ? (
    <div>
      <h1>{isError}</h1>
    </div>
  ) : (
    <div>
      <TodoInput handlePayload={handlePayload} />
      {todos.map((e) => (
        <div
          key={uuid()}
          style={{
            borderRadius: "5px",
            background: "rgba(0, 0, 0, 0.1)",
            boxShadow: "0 0 3px 0 black",
            width: "fit-content",
            margin: "auto"
          }}
        >
          <NavLink className={styles.todobox} to={`/todo/${e.id}`}>
            <p style={{ margin: "5px" }}>
              <h3>Title</h3> {e.task}
            </p>
            <p style={{ margin: "5px" }}>
              <h3>Date</h3> {e.date}
            </p>
            <p style={{ margin: "5px" }}>
              <h3>status</h3> {e.status === false ? "Pending" : "completed"}
            </p>
          </NavLink>
          <button onClick={() => handleToggle(e)} className={styles.btn}>
            Toggle
          </button>
          <button onClick={() => handleDelete(e)} className={styles.btn}>
            Delete
          </button>
        </div>
      ))}
    </div>
  );
};
