import { NavLink } from "react-router-dom";
import styles from "../css/navbar.module.css";
export const Navbar = () => {
  return (
    <div className={styles.nav}>
      <h1>Logo</h1>
      <ul>
        <NavLink className={styles.link} to="/todo">
          Todo
        </NavLink>
        <NavLink className={styles.link} to="/Total">
          Total
        </NavLink>
      </ul>
    </div>
  );
};
