import {
  ADD_TODO_FAILURE,
  ADD_TODO_REQUEST,
  ADD_TODO_SUCCESS,
  DELETE_TODO_FAILURE,
  DELETE_TODO_REQUEST,
  DELETE_TODO_SUCCESS,
  EDIT_TODO_FAILURE,
  EDIT_TODO_REQUEST,
  EDIT_TODO_SUCCESS,
  GET_TODO_FAILURE,
  GET_TODO_REQUEST,
  GET_TODO_SUCCESS,
  SHOW_TODO_FAILURE,
  SHOW_TODO_REQUEST,
  SHOW_TODO_SUCCESS,
  STATUS_COMPLETE_TODO_SUCCESS,
  TOTAL_TODO_SUCCESS
} from "./actionsType";

export const getTodoRequest = () => {
  return {
    type: GET_TODO_REQUEST
  };
};

export const getTodoSuccess = (payload) => {
  return {
    type: GET_TODO_SUCCESS,
    payload
  };
};

export const getTodoFailure = (payload) => {
  return {
    type: GET_TODO_FAILURE,
    payload
  };
};

