import {
  ADD_TODO_SUCCESS,
  ADD_TODO_REQUEST,
  ADD_TODO_FAILURE,
  GET_TODO_SUCCESS,
  GET_TODO_REQUEST,
  GET_TODO_FAILURE,
  SHOW_TODO_REQUEST,
  SHOW_TODO_SUCCESS,
  SHOW_TODO_FAILURE,
  EDIT_TODO_REQUEST,
  EDIT_TODO_SUCCESS,
  EDIT_TODO_FAILURE,
  DELETE_TODO_REQUEST,
  DELETE_TODO_SUCCESS,
  DELETE_TODO_FAILURE
} from "./actionsType";

const initState = {
  todo: [],
  loading: false,
  err: false,
  oneTodo: {}
};

export const reducer = (state = initState, { type, payload }) => {
  switch (type) {
    case GET_TODO_REQUEST:
      return {
        ...state,
        loading: true
      };

    case GET_TODO_SUCCESS:
      return {
        ...state,
        loading: false,
        err: false,
        todo: [...payload]
      };

    case GET_TODO_FAILURE:
      return {
        ...state,
        loading: false,
        err: payload
      };

    case ADD_TODO_REQUEST:
      return {
        ...state,
        loading: true
      };

    case ADD_TODO_SUCCESS:
      return {
        ...state,
        loading: false,
        err: false,
        todo: [...state.todo]
      };

    case ADD_TODO_FAILURE:
      return {
        ...state,
        loading: false,
        err: payload
      };

    case SHOW_TODO_REQUEST:
      return {
        ...state,
        loading: true
      };

    case SHOW_TODO_SUCCESS:
      return {
        ...state,
        loading: false,
        err: false,
        oneTodo: { ...payload }
      };
    case SHOW_TODO_FAILURE:
      return {
        ...state,
        loading: false,
        err: payload
      };

    case EDIT_TODO_REQUEST:
      return {
        ...state,
        loading: true
      };

    case EDIT_TODO_SUCCESS:
      return {
        ...state,
        loading: false,
        err: false,
        todo: [...state.todo]
      };
    case EDIT_TODO_FAILURE:
      return {
        ...state,
        loading: false,
        err: payload
      };

    case DELETE_TODO_REQUEST:
      return {
        ...state,
        loading: true
      };

    case DELETE_TODO_SUCCESS:
      return {
        ...state,
        loading: false,
        err: false,
        todo: [...state.todo]
      };
    case DELETE_TODO_FAILURE:
      return {
        ...state,
        loading: false,
        err: payload
      };

    default:
      return state;
  }
};
