import { useSelector } from "react-redux";

export const Total = () => {
  const todo = useSelector((state) => state.todo);
  const totalTodos = todo.length;
  const completedTodos = todo.filter((item) => item.status === true).length;

  return (
    <div>
      <h1>Total Todos are {totalTodos}</h1>
      <h1>Completed Todos are {completedTodos}</h1>
    </div>
  );
};
